'use client'
import { useState, useEffect } from 'react';
import GenderOptions from '../components/gender';
import { useRouter } from 'next/navigation';
import Loader from '../loader/page';
import { useDispatch } from 'react-redux';
import { setLocalName } from '../redux/slice';
import socket from '@/app/socket';

interface ModelGenderOptions {
    id: number;
    title: string;
    gender: null | string;
    genderSelection: string[]
}
const Main = () => {
    const router = useRouter()
    const [room, setRoom] = useState<number>(0)
    const [message, setMeassge] = useState("")
    const [name, setName] = useState<string>('')
    const dispatch = useDispatch();
    const initiateSearch = () => {
        dispatch(setLocalName(name))
        router.push('/loader');
        // socket.emit('createRoom', 'localName');
    }
    useEffect(() => {

        socket.on('info', data => {
            console.log(data);
        })

        return () => {
            socket.off('info')
        }
    }, [router]);
    const [genderOptions, setGenderOptions] = useState<ModelGenderOptions[]>([
        {
            id: 1,
            title: 'Ваш пол',
            gender: null,
            genderSelection: ['М', 'Ж']

        },
        {
            id: 2,
            title: 'Пол собеседника',
            gender: null,
            genderSelection: ['М', 'Ж']
        },
    ])

    // const hangleClick = (id, gender) => {
    //     setGenderOptions(genderOptions.map((option) =>
    //         option.id === id ? { ...option, gender: gender } : { ...option }))
    // }

    return (
        <>
            <main className="main-content">
                <div className="container">
                    <div className="intro-section-container">
                        <div className="intro-section">
                            <h1>В нашем мире анонимных знакомств каждое лицо - загадка, каждый человек - история.</h1>
                            <span>Помните, ответственность за вашу безопасность лежит на вас. Не передавайте личные данные и будьте осторожны.</span>
                        </div>
                        {/* <img className="main-image" src={img} alt="" /> */}
                    </div>
                    <form className="search-form" action="">
                        <div className="form-section">
                            {genderOptions.map(({ id, title, genderSelection }) => <GenderOptions key={id} id={id} title={title} genderSelection={genderSelection} />)}
                        </div>
                        <input type="text" onChange={(e) => setName(e.target.value)} className='text-input' placeholder='Ваше имя' />
                        <input type='button' onClick={() => initiateSearch()} value='Поиск' className="search-button" />
                    </form>
                </div>
            </main>
        </>
    )
}
export default Main;